.error-container{
    max-width: $max-width;
    margin: auto;
    flex-grow: 1;

    h1{
        font-weight: 700;
        font-size: 288px;
        color: $color-primary;
        margin-bottom: 10px;
    }
    p{
        color: $color-primary;
        text-align: center;
        font-size: 36px;
        margin-bottom: 20 0px;
    }
    a{
        display: block;
        text-decoration: underline;
        text-align: center;
        font-size: 18px;
    }
}

@media screen and (max-width: $break-point-mobile) {    
    .error-container{    
        h1{
            font-size: 96px;
        }
        p{
            font-size: 18px;
        }
        a{
            font-size: 14px;
        }
    }
}